.contacts {
  &__logos-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    
    @include onTablet {
      flex-direction: column;
    }

    @include onDesktop {
      flex-direction: column;
    }
  }

  &__social-logos {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    @include onTablet {
      width: 100%;
    }

    @include onDesktop {
      width: 100%;
    }
  }

  &__social-logo-facebook {
    margin-left: 6px;
    margin-right: 24px;
    
  }

  &__logo {
    @include onTablet {
      margin-bottom: 62px;
    }

    @include onDesktop {
      margin-bottom: 62px;
    }
  }

  &__subtitle {
    margin-bottom: 20px;

    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #0f0e08;
  }

  &__text {
    font-weight: 400;
    font-size: 13px;
    line-height: 150%;
    color: #0f0e08;

    margin-bottom: 64px;
    padding: 0;
  }

  &__border-line {
    border-bottom: 1px solid rgba(26, 90, 76, 0.5);
    margin-bottom: 40px;
    

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDesktop {
      display: block;
      border: 1px solid rgba(26, 90, 76, 0.5);
      width: 1px;
      height: 80%;
      margin: auto;
      justify-items: end;
      align-items: center;
    }
  }

  &__nav {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-bottom: 80px;

    @include onTablet {
      margin-bottom: 120px;
    }
  }

  &__nav-item {
    margin-bottom: 20px;
  }

  &__nav-link {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    text-decoration: none;
    color: #0f0e08;

    transition: border-color $animationSpeed;

    &:hover {
      font-weight: 600;
      color: green;
    }
  }

  margin-top: -50px;

  @include onTablet {
    margin-top: -40px;
  }

  @include onDesktop {
    margin-top: -150px;
  }
}
